<template>
  <div class="hospital">
    <doctor-options @search="search" ref="doctorOptions"/>
    <doctor-table @search="search" :docTotalSize="totalSize" :loading="loading" :data="data" :pageShow="pageShow" ref="doctorTable"/>
  </div>
</template>

<script>
import DoctorOptions from './controls/doctorOptions'
import DoctorTable from './controls/doctorTable'
import queryString from "querystring"

export default {
  name: 'doctor',
  data() {
    return {
      data:[], //table数据
      totalSize: 0, //一共多少条数据
      pageShow: false,  //页码切换是否显示
      loading: false
    }
  },
  components: {
    DoctorOptions,
    DoctorTable
  },
  mounted(){
    this.search();
  },
  methods:{
    search(){  //搜索
      this.loading = true;
      const optionParams = this.$refs.doctorOptions.getData();
      const tableParams = this.$refs.doctorTable.getData();
      let params = this.lodash.assign({}, optionParams, tableParams);
      const param = queryString.stringify(params || {});
      this.api.get(`/doctor?${param}`).then((res)=>{
        this.loading = false;
        if(!res.data) return;
        this.data = res.data.list;
        this.totalSize = res.data.totalPage;
        this.pageShow = this.data.length > 20;
        this.dataInit();
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    dataInit(){ //数据初始化
      this.data.map((item) => {
        if( item.country ) item.address = item.address || `${item.province}-${item.city}-${item.country}`;
        if( item.dept2 ) item.dept = item.dept || `${item.dept1}-${item.dept2}`;
      })
    }
  }
}
</script>
 
<style lang="less" scoped>

</style>
 